import React from 'react';
import '../css/estilos.css';
import Images from '../Imagenes/Images';

    
const Tecno = (props) =>{
    return(
        <div className="tecno">
            <div className="tecno__targ tecno__targ--1">
                <img className="tecno__img" src={Images.t1}></img>
                <div className="tecno__footer">
                </div>
            </div>
            <div className="tecno__targ tecno__targ--2">
                <img className="tecno__img" src={Images.t2}></img>
            </div>
            <div className="tecno__targ tecno__targ--3">
                <img className="tecno__img" src={Images.t3}></img>
            </div>
            <div className="tecno__targ tecno__targ--4">
                <img className="tecno__img" src={Images.t4}></img>
            </div>
        </div>
    );
}
export default Tecno;