import React, { Component } from 'react';
import '../css/estilos.css';
import Images from '../Imagenes/Images';
import $ from 'jquery';


    
class Principal extends React.Component {
 
  
  

    render() {
      <script src="http://ajax.googleapis.com/ajax/libs/jquery/1.3.2/jquery.min.js" type="text/javascript" charset="utf-8"></script>

      var accessToken = "6d05d1e00e46349012d0092c4bc2a461fcd63369";
      var deviceID = "1c0028000f47353136383631"
      var url = "https://api.particle.io/v1/devices/" + deviceID + "/girar";

      function switchOn()
      {
        $.post(url, {params: "on", access_token: accessToken });
        alert("Encendido");
      }  

      function switchOff()
      {
        $.post(url, {params: "off", access_token: accessToken });
        alert("Apagado");
      }
  
      return (
        
        <div className="c">
             <div className="c--header">
               <img src={Images.coca} className="c__img"></img>
            </div>
        
            <div className="c--footer">
              <div  className="c-f-r">
                <div>
                  <p>“Destapa la Felicidad”</p>
                  <p>“Open Happiness”</p>
                </div>
                <div>
                <button type="button" onClick={switchOn} value="ON" className="btn">Refrescate</button>
               
                </div>
                 
              </div>
              <div  className="c-f-r">
              <button type="button" onClick={switchOff} value="OFF"  className="btn">Detener</button>
              
                  
                  
              </div>
             </div>
        
        </div>
        
      );
    }
  }
export default Principal;




















